.header{
    background-color: #206A5D;
    color: white;
    &__inner{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__logo{
        margin: 15px 0;
    }
    &__menu{
        flex-grow: 1;
    }
    &__menu-list{
        height: 100%;
        display: flex;
        justify-content: space-around;
    }
    &__menu-list-item{
        flex-grow: 1;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 10px;
        padding: 18px 52px;
        &:hover{
            background-color: #288776;
            transition: all 0.3s ease-in-out;
        }
    }
    &__button{
        color: white;
        background-color: #F58634;
        border-radius: 10px;
        padding: 18px 52px;
        &:hover{
            background-color: #288776;
            transition: all 0.3s ease-in-out;
        }
    }
}